.main__graph-holder {
  height: 27.5rem;
  width: 27.5rem;
  margin: 0 auto 15vh auto;
  display: flex;
  align-items: center;
}
@media (max-width: 768px) {
  .main__graph-holder {
    margin: 0 auto 20vh auto;
  }
}
td {
  padding: 20px;
  border: 2px dashed green;
}
@media (max-width: 1280px) {
  .main__header-top.main__header-top--report {
    margin-bottom: 2.5rem;
  }
}
.tabs--report {
  margin: 2.375rem auto 0 auto;
}
