.main__timer {
  position: relative;
}
.main__timer-circle-animation {
  position: absolute;
  width: 253px;
  height: 253px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
}
.main__timer-btn {
  margin-bottom: 3.5rem;
}
.main__timer-ellipse {
  width: 253px;
  height: 253px;
  margin: 0 auto 4.3125rem auto;
  background-color: #2a3f50;
  box-shadow: 0px 5px 83px 17px rgba(21, 43, 59, 0.25);
  border-radius: 50%;
}
.main__timer-ellipse.main__timer-ellipse--work-category {
  border: 5px solid #ffb200;
}
.main__timer-ellipse.main__timer-ellipse--education-category {
  border: 5px solid #59abe3;
}
.main__timer-ellipse.main__timer-ellipse--hobby-category {
  border: 5px solid #b470d0;
}
.main__timer-ellipse.main__timer-ellipse--sport-category {
  border: 5px solid #e16c65;
}
.main__timer-ellipse.main__timer-ellipse--other-category {
  border: 5px solid #00d4d9;
}
.main__timer-info {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  align-items: center;
  color: #ffffff;
  font-size: 1.625rem;
}
.main__timer-min {
  font-size: 1.5rem;
  letter-spacing: 1.2px;
  color: #ffffff;
}
.main__timer-info-text {
  font-size: 0.9375rem;
  text-transform: uppercase;
}
.main__timer-info-time {
  font-size: 6.25rem;
  line-height: 1;
}
.main__timer-info-message {
  width: 50%;
  text-align: center;
}
@media (max-width: 768px) {
  .main .btn-timer-group {
    padding: 0 4.5rem;
  }
}
@media (max-width: 768px) {
  .main .btn-timer-group > * {
    padding-top: 1.625rem;
    padding-bottom: 1.625rem;
    flex-grow: 1;
  }
}
.progress-ring__circle {
  transition: stroke-dashoffset 0.31s linear, rotate 0.31s linear;
  transform: rotate(-90deg);
  transform-origin: 50% 50%;
}
