.header {
  padding-top: 4.125rem;
  padding-bottom: 1.3125rem;
  height: 5.875rem;
  background-color: #2a3f50;
  z-index: 1000;
  transition: max-height 0.5s;
  max-height: 0;
}
@media (max-width: 1280px) {
  .header {
    padding-top: 4.375rem;
  }
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .header {
    padding-top: 4.375rem;
  }
}
.header__container {
  display: flex;
  justify-content: space-between;
  height: 100%;
  align-items: center;
}
.header .logo {
  display: none;
  width: 15.125rem;
  height: 4.625rem;
}
.header .icon-add {
  display: none;
}
.header.is-fixed {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  max-height: 8.5rem;
  padding: 0;
  background-color: #223849;
  border: solid 1px #223849;
  box-shadow: 0 5px 8.3px 1.7px rgba(0, 0, 0, 0.2);
  z-index: 10000;
}
.header.is-fixed .logo {
  display: block;
}
.header.is-fixed .icon-add {
  display: block;
}
.nav {
  flex: 1 0 auto;
}
.nav__item {
  position: relative;
  cursor: pointer;
}
.nav__item-notification {
  position: absolute;
  bottom: -0.75rem;
  left: 0.375rem;
  display: block;
  width: 1.25rem;
  height: 1.25rem;
  font-size: 0.875rem;
  font-weight: bold;
  line-height: 1.5em;
  text-align: center;
  border-radius: 50%;
  background-color: #f75c4c;
  color: #ffffff;
  cursor: pointer;
}
@media (max-width: 1280px) {
  .nav__item-notification {
    width: 1.9375rem;
    height: 1.9375rem;
    font-size: 1.375rem;
  }
}
.nav__list {
  display: flex;
  justify-content: flex-end;
  flex-wrap: wrap;
}
.nav__link {
  color: #8da5b8;
}
@media (max-width: 1280px) {
  .nav__link {
    font-size: 26px;
  }
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .nav__link {
    font-size: 26px;
  }
}
.nav__link.hidden {
  display: none;
}
.nav__link:hover {
  color: #82c7e0;
}
.nav__item:not(:last-child) {
  margin-right: 2.0625rem;
}
@media (max-width: 1280px) {
  .nav__item:not(:last-child) {
    margin-right: 2.75rem;
  }
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .nav__item:not(:last-child) {
    margin-right: 2.75rem;
  }
}
