.popup {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1001;
  padding-top: 3.125rem;
  padding-bottom: 5.5rem;
}
@media (max-width: 768px) {
  .popup {
    padding: 0;
  }
}
.popup__controls {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 768px) {
  .popup__controls {
    margin-bottom: 52px;
  }
}
.popup__controls-left .icon {
  font-size: 1.375rem;
}
.popup__controls-right {
  display: flex;
  margin-left: auto;
}
.popup__controls-right .icon {
  font-size: 1.375rem;
}
@media (max-width: 768px) {
  .popup__controls-right .icon {
    font-size: 1.875rem;
  }
}
.popup__item {
  width: 31.25rem;
  height: 39.375rem;
  margin: 0 auto;
  padding: 0.875rem 0.9375rem;
  background-color: #2a3f50;
  z-index: 1000;
}
@media (max-width: 1280px) {
  .popup__item {
    width: 35.3125rem;
    height: 44.125rem;
  }
}
@media (max-width: 768px) {
  .popup__item {
    width: 100%;
    height: 100%;
    padding: 2.1875rem 1.875rem;
  }
}
.popup__form {
  display: flex;
  flex-direction: column;
  padding: 0 1.75rem;
}
@media (max-width: 768px) {
  .popup__form {
    padding: 0.625rem 3.4375rem;
  }
}
.popup__header {
  margin-bottom: 1rem;
  text-align: center;
  color: #ffffff;
}
@media (max-width: 1280px) {
  .popup__header {
    margin-bottom: 1.875rem;
  }
}
.popup .form-btn:not(:last-child) {
  margin-right: 0.75rem;
}
@media (max-width: 768px) {
  .popup .form-btn:not(:last-child) {
    margin-right: 1.875rem;
  }
}
.popup__footer {
  display: flex;
  justify-content: center;
}
.popup__footer .btn {
  margin: 0 10px;
}
@media (max-width: 1280px) {
  .popup__footer .btn {
    font-size: 1.125rem;
    padding: 0.875rem 3.625rem;
  }
}
.popup .message {
  width: 75%;
  margin: 8.75rem auto 11.25rem auto;
}
@media (max-width: 1280px) {
  .popup .message {
    width: 66%;
    margin: 11.875rem auto 13rem auto;
  }
}
@media (max-width: 768px) {
  .popup .message {
    width: 90%;
    margin: 17.625rem auto 20.125rem auto;
  }
}
.form {
  color: #ffffff;
}
@media (max-width: 768px) {
  .form {
    font-size: 1.125rem;
  }
}
.form__group {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.75rem;
  border-bottom: 1px solid #425869;
}
@media (max-width: 768px) {
  .form__group {
    margin-bottom: 2.375rem;
  }
}
.form__group-input {
  line-height: 2;
}
@media (max-width: 768px) {
  .form__group-input {
    font-size: 1.125rem;
  }
}
.form input[type="date"],
.form .fieldset {
  margin-bottom: 0.625rem;
}
.form__group-input::placeholder {
  color: #8da5b8;
}
.form__group-title {
  margin-bottom: 0.625rem;
  font-size: 0.875rem;
  color: #ffffff;
  text-transform: uppercase;
}
@media (max-width: 1280px) {
  .form__group-title {
    margin-bottom: 1.125rem;
  }
}
@media (max-width: 768px) {
  .form__group-title {
    font-size: 1rem;
  }
}
.form .radio {
  display: inline-flex;
  align-items: center;
  margin: 0 0.375rem;
  cursor: pointer;
}
.form .radio:hover {
  color: #82c7e0;
}
.form .radio__input {
  display: none;
}
.form .radio .radio__input:checked ~ span {
  color: #ffffff;
}
.form .radio__box {
  display: inline-block;
  width: 0.625rem;
  height: 0.625rem;
  margin-right: 0.625rem;
  border-radius: 50%;
  background-color: #8da5b8;
  box-shadow: 0 0 0 5px #8da5b8;
  cursor: pointer;
}
.form .radio__input:checked + .radio__box {
  box-shadow: 0 0 0 5px #8da5b8;
}
.form .radio--urgent .radio__input:checked + .radio__box {
  box-shadow: 0 0 0 5px #f15a4a;
  background-color: #2a3f50;
}
.form .radio--urgent .radio__input:checked + .radio__box:before {
  position: relative;
  right: -3px;
  top: 3px;
  display: block;
  content: '';
  width: 4px;
  height: 4px;
  border-radius: 50%;
  background-color: #f15a4a;
}
.form .radio--urgent:disabled + .check__box {
  background-color: #ffffff;
}
.form .radio--urgent:hover .radio__box {
  background-color: #f15a4a;
  box-shadow: 0 0 0 5px #f15a4a;
}
