.box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 76%;
  margin: 0 auto;
  padding: 9vh 0 2vh 0;
}
@media (max-width: 1280px) {
  .box {
    width: 82%;
    justify-content: space-evenly;
    padding: 14vh 0 6vh 0;
  }
}
@media (max-width: 768px) {
  .box {
    padding: 6vh 0 6vh 0;
  }
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .box {
    width: 100%;
    align-items: center;
    justify-content: center;
    padding: 13vh 0 6vh 0;
  }
}
.box__item {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  flex-basis: 20.875rem;
  margin: 0 1.25rem 3.75rem 0;
  padding-left: 2.3125rem;
  font-size: 0.875rem;
  font-family: "PT Sans", sans-serif;
}
@media (max-width: 1280px) {
  .box__item {
    margin: 0 0rem 5.125rem 0;
  }
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .box__item {
    min-width: 24.6875rem;
    margin: 0 0rem 5.125rem 0;
  }
}
@media (max-width: 768px) {
  .box__item {
    min-width: 22.1875rem;
    margin: 0 0 2.75rem 0;
  }
}
.box__item:before {
  position: absolute;
  display: block;
  content: " ";
  top: 8px;
  left: 0;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background-color: transparent;
}
.box__item--yellow:before {
  background-color: #ffb200;
}
.box__item--aqua-blue:before {
  background-color: #00d4d9;
}
.box__item--dark-blue:before {
  background-color: #59abe3;
}
.box__item--orchid:before {
  background-color: #b470d0;
}
.box__item--dark-peach:before {
  background-color: #e16c65;
}
@media (max-width: 768px) {
  .box__item:last-child {
    margin-bottom: 0;
  }
}
.box__heading {
  flex-basis: 6.75rem;
  font-size: 0.875rem;
  text-transform: uppercase;
  margin-right: 1.75rem;
  color: #ffffff;
}
@media (max-width: 1280px) {
  .box__heading {
    flex-basis: 8.75rem;
    font-size: 1rem;
    margin-right: 0;
  }
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .box__heading {
    flex-basis: 8.125rem;
    font-size: 1rem;
    margin-right: 0;
  }
}
.box__desk {
  flex-basis: 100%;
  margin-top: 0.875rem;
}
.box--column {
  flex-direction: column;
  width: auto;
}
.box--column .box__item {
  flex-basis: auto;
  min-width: 18.125rem;
  margin: 0 auto 2.25rem auto;
}
@media (max-width: 1280px) {
  .box--column .box__item {
    margin: 0px auto 2.75rem auto;
  }
}
@media (max-width: 768px) {
  .box--column .box__item {
    min-width: 100%;
  }
}
.box--column .box__heading {
  flex-grow: 1;
  margin: 0;
  padding-bottom: 0.3125rem;
  text-transform: capitalize;
  color: #8da5b8;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1rem;
  border-bottom: 1px solid #425869;
}
@media (max-width: 1280px) {
  .box--column .box__heading {
    min-width: 26.25rem;
    font-size: 1.1875rem;
  }
}
@media (max-width: 768px) {
  .box--column .box__heading {
    font-size: 1.4375rem;
  }
}
.input-group {
  width: 9.0625rem;
  display: flex;
  border-bottom: 1px solid #8da5b8;
  font-size: 1rem;
  font-family: "Roboto", sans-serif;
}
.input-group:hover {
  border-bottom: 1px solid #82c7e0;
}
.input-group:hover .input-group__digit {
  color: #ffffff;
}
@media (max-device-width: 1024px) and (orientation: landscape) {
  .input-group {
    width: 8.875rem;
  }
}
.input-group__control {
  width: 0.75rem;
}
.input-group__control:hover {
  color: #82c7e0;
}
.input-group__control:hover .input-group__digit {
  color: #ffffff;
}
.input-group__control .icon {
  font-size: 11px;
  vertical-align: middle;
  pointer-events: none;
}
.input-group__control:disabled {
  pointer-events: none;
}
.input-group__digit {
  flex-grow: 1;
  width: 0;
  text-align: center;
  background: transparent;
  outline: none;
}
.header-cycle {
  margin-bottom: 1.5625rem;
  font-family: "PT Sans", sans-serif;
  font-size: 1.25rem;
  font-weight: bold;
  text-align: center;
  color: #ffffff;
}
@media (max-width: 768px) {
  .header-cycle {
    margin-bottom: 2.375rem;
  }
}
.box-cycle {
  font-family: "PT Sans", sans-serif;
  font-size: 12px;
}
.box-cycle__line {
  display: flex;
  height: 10px;
  width: 100%;
  margin-bottom: 10px;
}
.box-cycle__work-time {
  background-color: #ffb200;
}
.box-cycle__short-break {
  background-color: #57a6dc;
}
.box-cycle__long-break {
  background-color: #b470d0;
}
.box-cycle__label {
  position: relative;
}
.box-cycle__label::after {
  content: "";
  position: absolute;
  right: 0;
  left: 0;
  display: block;
  border-radius: 50%;
  width: 3px;
  height: 3px;
  background-color: #00d4d9;
}
.box-cycle__label-top {
  display: flex;
  height: 10px;
  margin-bottom: 10px;
}
.box-cycle__label-top .box-cycle__label {
  position: relative;
}
.box-cycle__label-top .box-cycle__label:nth-child(2) .box-cycle__label-text {
  right: 50%;
  transform: translateX(50%);
}
.box-cycle__label-top .box-cycle__label:last-child {
  margin-left: auto;
}
.box-cycle__label-top .box-cycle__label:last-child .box-cycle__label-text {
  right: 0;
}
.box-cycle__label-top .box-cycle__label-text {
  position: absolute;
  bottom: 10px;
  width: max-content;
}
.box-cycle__label-bottom {
  display: flex;
  height: 10px;
}
.box-cycle__label-bottom .box-cycle__label {
  position: relative;
}
.box-cycle__label-bottom .box-cycle__label-text {
  position: absolute;
  bottom: -10px;
  right: 50%;
  transform: translateX(50%);
}
